





































































































































































































































































































































































[v-cloak] {
    display: none;
}

.pos-re {
    position: relative;
}

.vedioBox {
    width: 100%;
    height: 12rem;
    overflow: hidden;
}

.online {
    width: 4.475rem;
    height: 2.25rem;
    background: rgba(0, 0, 0, 0.15);
    color: #fff;
    font-size: 0.625rem;
    line-height: 1.125rem;
    position: absolute;
    bottom: 0.75rem;
    left: 0.75rem;
    border-radius: 0.2rem;
    padding-left: 0.375rem;
}

.msgBox {
    height: 6rem;
    padding: 0 0.75rem;
    background-color: #fff;
}

.msgBox h3 {
    font-size: 0.75rem;
    line-height: 2.425rem;
}

.msgBox span {
    display: inline-block;
    margin-left: 0.375rem;
    line-height: 1.125rem;
    font-size: 0.625rem;
}

.msgBox span i {
    color: #333;
    font-weight: bold;
}

.touPic {
    width: 2.375rem;
    height: 2.375rem;
    border-radius: 50%;
    overflow: hidden;
    float: left;
}

.liver i {
    padding-right: 1.0rem;
    background-position: 0.13rem -0.15rem;
    background-size: 0.7rem 1rem;
    background-repeat: no-repeat;
}

.liver i.girl {
    background-image: url(/static/liveStreaming/img/girl.png);
}

.liver i.boy {
    background-image: url(/static/liveStreaming/img/boy.png);
}

.shar {
    position: relative;
    float: right;
    width: 1.6rem;
    height: 2.125rem;
    margin-top: -1rem;
    text-align: center;
}

.shar::before {
    content: '';
    position: absolute;
    left: -0.7rem;
    height: 100%;
    width: 0;
    border-left: 1px solid #e1e1e1;
    padding-left: 10px;
}

.shar i {
    display: block;
    width: 100%;
    height: 1.125rem;
    background: url(/static/liveStreaming/img/share.png) no-repeat;
    background-position: center center;
    background-size: 1.2rem 1rem;
}

.shar span {
    margin: 0;
}

.liveList {
    margin-top: 0.5rem;
    background-color: #fff;
}

.listTab li {
    width: 50%;
    height: 2.5rem;
    line-height: 2.5rem;
    font-size: 0.75rem;
    color: #555;
    float: left;
    box-sizing: border-box;
    overflow: hidden;
}

.listTab .active {
    color: #FFAF00;
    border-bottom: 2px solid #FFAF00;
}

/* 分享浮层 */
.toshar {
    width: 100%;
    height: 100%;
    z-index: 50;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.45);
    color: #fff;
}

.toshar i {
    display: inline-block;
    width: 2.625rem;
    height: 5.5rem;
    background: url(/static/liveStreaming/img/arrow.png) no-repeat;
    background-size: 100%;
    float: right;
    margin-top: 1.25rem;
    margin-right: 0.75rem;
}

.toshar p {
    font-size: 0.75rem;
    float: right;
    margin-top: 5.625rem;
    margin-right: 0.5rem;
}




